footer{
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}
footer a{
  color:var(--color-bg);

}
.footer__logo{
  font-size: 3rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;

}
.footer__details{
  margin-bottom: 1.5rem;
  font-size: 1.5rem;

}
.permalinks{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap:2rem;
  margin:0 auto 3rem;

}
.footer__socials{
  display: flex;
  justify-content: center;
  gap:1rem;
  margin-bottom: 4rem;
}
.footer__socials a{
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
border:1px solid transparent;
}
.footer__socials a:hover{
background: transparent;
color:var(--color-bg);
border-color:var(--color-bg) ;
}
